import React, { Component } from 'react';
import 'react-widgets/dist/css/react-widgets.css';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
  Container,
} from 'reactstrap';
import {Combobox} from 'react-widgets';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table'
import 'react-widgets/dist/css/react-widgets.css';
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import axios from 'axios';
import { saveAs } from 'file-saver';

class ChequeForm extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      companies:[],
      company:[],companyPayments:[],company2:[],
      issueDate: new Date(), realiseDate: new Date(),
      startDate: new Date(), endDate: new Date(),
      amount:0,chequeNo:'',bank:'',cellInfo:[],cheques2:[],
      inTotal:0,inBalance:0,inTotal2:0,
      timeout: 300
    };
    this.onChange=this.onChange.bind(this);
  }

  componentDidMount(){
    this.state.startDate.setDate(this.state.startDate.getDate()-31)
    this.getCompanies();
    this.getCheques();
  }
  onChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }
  //fetch companis from database
  getCompanies(){
    fetch('/getcompanies')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        companies : json
      });
    });
  }
  addCheque= async()=>{
    var idate = this.state.issueDate.getFullYear() + '-' + (this.state.issueDate.getMonth() + 1) + '-' + this.state.issueDate.getDate();
    var rdate = this.state.realiseDate.getFullYear() + '-' + (this.state.realiseDate.getMonth() + 1) + '-' + this.state.realiseDate.getDate();
    try{
    await axios.post('/addcheque',{
      chequeNo : this.state.chequeNo,
      issueDate :idate,
      realiseDate: rdate,
      amount : this.state.amount,
      company: this.state.company.companyId,
      bank : this.state.bank
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('Successfull')){
        alert('Successfully Added');
        this.onIncomeAdd();
      }
      else{
        alert('Bad Credentials')
      }
    })
  }catch(e){
    console.log(e);
    alert("Failed To Add")
  }
  }
  //get cheque details
  getCheques=async()=>{
    await axios.post('/getcheque',{
      startDate:this.state.startDate,
      endDate:this.state.endDate
    }).then(res=>{
      this.setState({
        cheques:res.data
      })
    })
    var total=this.state.inTotal;
    for(var i=0; i<this.state.cheques.length; i++){

        total+=this.state.cheques[i].chequesAmount;

    }
    this.setState({
      inTotal:total
    })
    if(this.state.cheques.length==0){
      this.setState({
        inTotal:0
      })
    }
  }
  //get cheques by company
  getChequesByCmp=async()=>{
    await axios.post('/getchequebycmp',{
      startDate:this.state.startDate,
      endDate:this.state.endDate,
      company:this.state.company2.companyId
    }).then(res=>{
      this.setState({
        cheques2:res.data
      })
    })
    var total=0;
    for(var i=0; i<this.state.cheques2.length; i++){

        total+=this.state.cheques2[i].chequesAmount;

    }
    this.setState({
      inTotal2:total
    })
    if(this.state.cheques2.length==0){
      this.setState({
        inTotal2:0
      })
    }
  }
    // update company payments table
    onIncomeAdd= async()=>{
      await axios.post('/getpay',{
        company : this.state.company.companyId
      })  .then(res =>{
        this.setState({ 
          companyPayments : res.data,
          
        });
      });

      await axios.post('/updatepay',{
        company : this.state.company.companyId,
        credit : this.state.companyPayments[0].companyPaymentsCredit + parseFloat(this.state.amount),
        debit : this.state.companyPayments[0].companyPaymentsDebit
      }).then(res =>{
        var status=res.data.status;
        if(!status.localeCompare('UnSuccessfull')){
          alert('Error please contact developer');
        }
      })
      this.getCompanyPayments(this.state.company);
  }
  // when deleting a payment
  onIncomeRemove= async(cell)=>{
    await axios.post('/getpay',{
      company : this.state.cellInfo[0].companyId
    })  .then(res =>{
      this.setState({ 
        companyPayments : res.data,
        
      });
    })
    await axios.post('/updatepay',{
      company : this.state.cellInfo[0].companyId,
      credit : this.state.companyPayments[0].companyPaymentsCredit - parseFloat(this.state.cellInfo[0].chequesAmount),
      debit : this.state.companyPayments[0].companyPaymentsDebit
    }).then(res =>{
      var status=res.data.status;
      if(!status.localeCompare('UnSuccessfull')){
        alert('Error please contact developer');
      }
    })
    this.getCompanyPayments(this.state.company);
}
  getCompanyPayments=async(cmp)=>{
    this.setState({
      inBalance:0
    })
    try{
      await axios.post('/getpay',{
        company : cmp.companyId
      })  .then(res =>{
        this.setState({ 
          inBalance : (res.data[0].companyPaymentsDebit - res.data[0].companyPaymentsCredit).toFixed(2) ,
          
        });
      });
    }catch(e){
      console.log(e);
    }
  }

  //handle date changes
  handleIssueDateChange = date =>{
    this.setState({
      issueDate:date,
    })
  };
  handleRealiseDateChange = date =>{
    this.setState({
     realiseDate:date,
    })
    
  };
  handleStartDateChange = date =>{
    this.setState({
      startDate:date,
      inTotal:0
    },()=>{
      this.getCheques();
    })
    
  };
  handleEndDateChange = date =>{
    this.setState({
     endDate:date,
     inTotal:0
    })
    this.getCheques();
    
  };
  //clear form
  clearForm=()=>{
    this.setState({
      chequeNo:'',
      amount:'',
      bank:'',
    })
  }

  //date formatter
  dateFormater(cell , row){
    var stDate = new Date(cell);
    var date = stDate.getFullYear() + '-' + (stDate.getMonth() + 1) + '-' + stDate.getDate();
    return `${date}`
  }
    // price formatter
    priceFormat(cell){
      return cell.toFixed(2);
    } 
  //Event handlers for table
onRowSelect=(rowKeys)=>{
  this.setState({
    companyRegNo : rowKeys.companyRegNo,
    companyName: rowKeys.companyName,
    companyAddress: rowKeys.companyAddress,
    companyEmail:rowKeys.companyEmail,
    companyContact: rowKeys.companyContact,
    btnText:'Update',
    btnColor:'info'
  })
}

onAfterDeleteRow= async (rowKeys,cell)=> {

  this.setState({
    cellInfo :cell
  })

  await axios.post('/deletecheque',{
    chequeNo : rowKeys,
  }).then(res =>{
    var status=res.data.status;
    if(!status.localeCompare('Successfull')){
      alert('Deleted');
      this.onIncomeRemove();
      this.getCheques();
       }
    else{
      alert ('Failed To Delete');
    }
  })
}

selectRowProp={
  mode:'radio',
  onSelect: this.onRowSelect,
  bgColor:'yellow'
}

options = {
  afterDeleteRow: this.onAfterDeleteRow  // A hook for after droping rows.

};
onGetChequeReport= async()=>{
  var stdate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
  var endate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
 await axios.post(`/getchequeRp`, {
    range : 'From : '+ stdate +' To :'+endate,
    cheques: this.state.cheques,
    total : this.state.inTotal
   })
   .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
   .then((res) => {
     const pdfBlob = new Blob([res.data], { type: 'application/pdf' });

     saveAs(pdfBlob, `ChequesReport.pdf`);
   })
  }
  onGetChequeReportByCmp= async()=>{
    var stdate = this.state.startDate.getFullYear() + '-' + (this.state.startDate.getMonth() + 1) + '-' + this.state.startDate.getDate();
    var endate = this.state.endDate.getFullYear() + '-' + (this.state.endDate.getMonth() + 1) + '-' + this.state.endDate.getDate();
   await axios.post(`/getchequebycpmRp`, {
      range : 'From : '+ stdate +' To :'+endate,
      company : this.state.company2.companyName,
      cheques: this.state.cheques2,
      total : this.state.inTotal2
     })
     .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
     .then((res) => {
       const pdfBlob = new Blob([res.data], { type: 'application/pdf' });
  
       saveAs(pdfBlob, `ChequesReport-${this.state.company2}.pdf`);
     })
    }
  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" sm="6">
            <Card>
              <CardHeader>
                <strong>Incomes</strong>
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
             
              <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="company">Company</Label>
                    </Col>
                    <Col xs="12" md="9">
                    <div>
                      <Combobox
                        data={this.state.companies}
                        suggest={true}
                        //valueField='companyName'
                        textField='companyName'
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value)=>{
                          this.setState({
                            company:value
                          },()=>{
                            this.getCompanyPayments(this.state.company);
                          });
                        }
                        }
                      />
                  </div>
                    </Col>
                  </FormGroup>
                  <FormGroup>
                  <Input type="text" name="inBalance" placeholder={`You Have Rs: ${this.state.inBalance} Total Balance From This Comapany`} disabled/>
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="chequeNo">Cheque Number</Label>
                  <Input type="text" name="chequeNo" placeholder="" value={this.state.chequeNo} onChange={this.onChange}/>
                </FormGroup>
                <FormGroup>
                  <Label htmlFor="chequeNo">Bank</Label>
                  <Input type="text" name="bank" placeholder="" value={this.state.bank} onChange={this.onChange}/>
                </FormGroup>
                <FormGroup row>
                 
                 <MuiPickersUtilsProvider utils={DateFnsUtils}>
                   <Grid container justify="space-around">
                     <Col >
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="Issue Date"
                       format="yyyy-MM-dd"
                       value={this.state.issueDate}
                       onChange={this.handleIssueDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     <Col >
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="Realise Dates"
                       format="yyyy-MM-dd"
                       value={this.state.realiseDate}
                       onChange={this.handleRealiseDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     </Grid>
                   </MuiPickersUtilsProvider>
                   </FormGroup>
                   <FormGroup>
                        <Label htmlFor="appendedPrependedInput">Amount</Label>
                        <div className="controls">
                          <InputGroup className="input-prepend">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>Rs</InputGroupText>
                            </InputGroupAddon>
                            <Input name="amount" size="16" type="text" value={this.state.amount}
                            onChange={this.onChange}/>
                          </InputGroup>
                        </div>
                      </FormGroup>
                  <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={()=>{this.addCheque(); this.getCheques()}}>Save</Button>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={()=>this.clearForm()}>clear</Button>
                    </FormGroup>
                  </Col>
                  </FormGroup>
              </CardBody>
              <CardFooter>
                <FormGroup row></FormGroup>
                <FormGroup row></FormGroup>
                <FormGroup row></FormGroup>
                <FormGroup row></FormGroup>
              </CardFooter>
            </Card>
          </Col>
          <Col xs="6" ys="6">
          <Card>
            <CardHeader>
              <Label>Incomes From Companies</Label>
              <FormGroup row>
                 
                 <MuiPickersUtilsProvider utils={DateFnsUtils}>
                   <Grid container justify="space-around">
                     <Col >
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="Start Date"
                       format="yyyy-MM-dd"
                       value={this.state.startDate}
                       onChange={this.handleStartDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     <Col >
                     <KeyboardDatePicker
                       margin="normal"
                       id="date-picker-dialog"
                       label="End Dates"
                       format="yyyy-MM-dd"
                       value={this.state.endDate}
                       onChange={this.handleEndDateChange}
                       KeyboardButtonProps={{
                       'aria-label': 'change date',
                       }}
                     />
                     </Col>
                     </Grid>
                   </MuiPickersUtilsProvider>
                   </FormGroup>
            </CardHeader>
            <CardBody style={{backgroundColor:"#d9d9d9"}}>
              <BootstrapTable data={this.state.cheques} scrollTop={'Bottom'} pagination height="300"
              selectRow={this.selectRowProp} deleteRow={true} options={this.options}
              tableStyle={ { border: '#0000FF 2.5px solid' } }
              containerStyle={ { border: '#FFBB73 2.5px solid' } }
              headerStyle={ { border: 'red 1px solid' } }
              bodyStyle={ { border: 'green 1px solid' }}>
                      <TableHeaderColumn width="150" dataField="chequesNumber" isKey>Cheque No</TableHeaderColumn>
                      <TableHeaderColumn width="150" dataField="companyName">Comapany</TableHeaderColumn>
                      <TableHeaderColumn width="150" dataField="chequesRealiseDate" dataFormat={this.dateFormater}>Realise Date</TableHeaderColumn>
                      <TableHeaderColumn width="150" dataField="chequesIssueDate" dataFormat={this.dateFormater}>Issue Date</TableHeaderColumn>
                      <TableHeaderColumn width="120" dataField="chequesBank">Bank</TableHeaderColumn>
                      <TableHeaderColumn width="120" dataField="chequesAmount" tdStyle={{'text-align' : 'right'}} dataFormat={this.priceFormat.bind(this)}>Amount(Rs)</TableHeaderColumn>
              </BootstrapTable>
            </CardBody>
            <CardFooter>
              <FormGroup row>
                <Col xs="8">
                    <Label>Total Incomes (Rs) : {this.state.inTotal.toFixed(2)} </Label>
                </Col>
                <Col>
                <Button color="info" onClick={()=>{this.onGetChequeReport()}}>Cheque Report</Button>
                </Col>
              </FormGroup>
            </CardFooter>
          </Card>
          </Col>
          </Row>
          <Row>
          <Col  xs="12" ys="">
          <Card>
          <CardHeader>
            
              <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="company">Company</Label>
                    </Col>
                    <Col xs="5" md="3">
                    <div>
                      <Combobox
                        data={this.state.companies}
                        suggest={true}
                        //valueField='companyName'
                        textField='companyName'
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value)=>{
                          this.setState({
                            company2:value
                          },()=>{
                            this.getChequesByCmp();
                            this.getCompanyPayments(this.state.company2);
                          });
                        }
                        }
                      />
                  </div>
                    </Col>
                  </FormGroup>
                  <FormGroup>
                  
                  <Input type="text" name="inBalance" placeholder={`You Have Rs: ${this.state.inBalance} Total Balance From This Comapany`} disabled/>
                
                  </FormGroup>
                
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
              <BootstrapTable data={this.state.cheques2} scrollTop={'Bottom'} pagination height="300"
              selectRow={this.selectRowProp} deleteRow={true} options={this.options}
              tableStyle={ { border: '#0000FF 2.5px solid' } }
              containerStyle={ { border: '#FFBB73 2.5px solid' } }
              headerStyle={ { border: 'red 1px solid' } }
              bodyStyle={ { border: 'green 1px solid' }}>
                      <TableHeaderColumn width="150" dataField="chequesNumber" isKey>Cheque No</TableHeaderColumn>
                      <TableHeaderColumn width="150" dataField="companyName">Comapany</TableHeaderColumn>
                      <TableHeaderColumn width="150" dataField="chequesRealiseDate" dataFormat={this.dateFormater}>Realise Date</TableHeaderColumn>
                      <TableHeaderColumn width="150" dataField="chequesIssueDate" dataFormat={this.dateFormater}>Issue Date</TableHeaderColumn>
                      <TableHeaderColumn width="120" dataField="chequesBank">Bank</TableHeaderColumn>
                      <TableHeaderColumn width="120" dataField="chequesAmount" tdStyle={{'text-align' : 'right'}} dataFormat={this.priceFormat.bind(this)}>Amount(Rs)</TableHeaderColumn>
              </BootstrapTable>
            </CardBody>
            <CardFooter>
              <FormGroup row>
                <Col xs="10">
                    <Label>Total Incomes (Rs) : {this.state.inTotal2.toFixed(2)} </Label>
                </Col>
                <Col>
                <Button color="info" onClick={()=>{this.onGetChequeReportByCmp()}}>Cheque Report</Button>
                </Col>
              </FormGroup>
            </CardFooter>
          </Card>
          </Col>
          
        </Row>
        
      </div>
    );
  }
}

export default ChequeForm;
